// Default values for all variables (midpoint of scale)
export const getDefaultValues = () => {
  const defaults = {};
  Object.keys(VARIABLES_DEFINITION).forEach(key => {
    defaults[key] = 3; // Assuming 1-5 Likert scale, 3 is neutral
  });
  return defaults;
};

